	module.exports = {"data":{"wordpressPost":{"title":"The Sydney hair salon celebrities love","content":"<p><strong>Australia</strong> is a lucky country and for a number of very significant reasons &#8211; you know, freedom, safety, all the important stuff. We&#8217;re also pretty blessed when it comes to beauty talent. Here, in the first of our <strong>Meet the Talent</strong> series, we check in with Sydney celebrity stylist and sweet-talking charmer <strong>George Giavis</strong>.</p>\n<p>So lovely George &#8211; let&#8217;s begin &#8230; Ahem. Please answer the following.</p>\n<p><strong>Name</strong>: George Giavis</p>\n<p><strong>Years in the Business</strong>: 20 years</p>\n<p><strong>Started Out By Doing</strong>:<br />\nWould you believe I started out doing a<strong> Science Degree</strong>, but my real love was always hairdressing. My aunt owned a hairdressing salon so I guess it’s in my blood. I always had a fascination with fashion, the beauty industry and the art of transformation. Science does how ever have its uses and I guess that&#8217;s why I was always so interested in the colouring process, mixing custom colours involves science, just without having to wear a white lab coat.</p>\n<p><strong>Worst Gig</strong>:<br />\nI worked as the bell boy in a<strong> very plush Sydney hotel</strong> in the early 80&#8217;s. Whilst the work was not really me, I had many celebrity encounters, witnessed some hair-raising behaviour, learned the art of discretion and got up close and personal with my<strong> idol Gianni Versace.</strong></p>\n<img class=\"alignnone size-medium wp-image-1251\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/26160606/GeorgeGiavis-212x300.jpg\" alt=\"\" width=\"212\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/26160606/GeorgeGiavis-212x300.jpg 212w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/26160606/GeorgeGiavis-768x1087.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/26160606/GeorgeGiavis-723x1024.jpg 723w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/26160606/GeorgeGiavis.jpg 1239w\" sizes=\"(max-width: 212px) 100vw, 212px\" /><img class=\"alignnone size-medium wp-image-1256\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/26162033/i-019349-radiance-perfected-tinted-moisturiser-no1-alabasta-1-940-300x300.jpg\" alt=\"\" width=\"300\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/26162033/i-019349-radiance-perfected-tinted-moisturiser-no1-alabasta-1-940-300x300.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/26162033/i-019349-radiance-perfected-tinted-moisturiser-no1-alabasta-1-940-150x150.jpg 150w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/26162033/i-019349-radiance-perfected-tinted-moisturiser-no1-alabasta-1-940-768x768.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/26162033/i-019349-radiance-perfected-tinted-moisturiser-no1-alabasta-1-940.jpg 940w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p><em>The very handsome and frankly, ultra talented Mr George Giavis. Did we mention he&#8217;s handsome? He&#8217;s very handsome. And<a href=\"http://mecca.com.au\"> Eve Lom&#8217;s Radiance Perfected Tinted Moisturiser, $72</a>. Actually does what it says!</em></p>\n<p><strong>Best Gig:</strong><br />\nWhilst I have been fortunate to work many red carpet events, like the <strong>Golden Globes</strong> and style and colour the hair of many famous people,  my best gig was styling my best friends hair for her wedding. Friends and family are everything.</p>\n<p><strong>Breakthrough Moment:</strong><br />\nI was working in London at an upmarket salon and one of my wealthy clients asked for my salon details for when she travelled to Australia. It was a call to action and I vowed that when I returned to Australia it would be to work in my own salon.</p>\n<p><strong>You Love Hair Dressing Because:</strong><br />\nA great hair colour or cut does wonders for a woman&#8217;s self esteem. I love the power of a beauty transformation. Watching a woman walk out of my salon feeling more <strong>confident and beautiful</strong> always reminds me of my love of hairdressing.</p>\n<img class=\"alignnone size-medium wp-image-1250\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/26160547/blonderoom2-212x300.jpg\" alt=\"\" width=\"212\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/26160547/blonderoom2-212x300.jpg 212w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/26160547/blonderoom2-768x1087.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/26160547/blonderoom2-723x1024.jpg 723w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/26160547/blonderoom2.jpg 1239w\" sizes=\"(max-width: 212px) 100vw, 212px\" /><img class=\"alignnone size-medium wp-image-1275\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/26182813/i-007964-cleanser-50ml-1-940-300x300.jpg\" alt=\"\" width=\"300\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/26182813/i-007964-cleanser-50ml-1-940-300x300.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/26182813/i-007964-cleanser-50ml-1-940-150x150.jpg 150w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/26182813/i-007964-cleanser-50ml-1-940-768x768.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/26182813/i-007964-cleanser-50ml-1-940.jpg 940w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p><em>George&#8217;s salon The Blonde Room. Don&#8217;t worry. Blondes and brunettes are welcome too. Also, <a href=\"http://mecca.com.au\">Eve Lom&#8217;s Cleanser, $72,</a> and worth every cent. </em></p>\n<p><strong>At your Salon you Provide:</strong><br />\nWe are not a &#8216;gold fish bowl&#8217; salon. Each client has their own suite, so we offer the utmost privacy. Long haul hairdressing involving colour requires a client to spend a fair amount of time at the salon. We try to make this time as pleasurable as possible.<br />\nI am also not aligned to any one hair care brand. We choose the best products and colours from every range. I like the reds from one, the blondes from another.<br />\nOur salon was one of the first colour specialist salons in the country. We offer meticulous colouring techniques. I try to make my blondes as natural and seamless as possible. I am known for my blonde techniques and we have a V.I.P room appropriately named <strong>The Blonde Room</strong>.</p>\n<p><strong>Added Extras:</strong><br />\nMy mother is a wonderful cook and makes many delicious <strong>Greek cakes and treats</strong>. Our clients love them.  Some even call to see what&#8217;s on the menu before they arrive. I believe that when it comes to making a hair statement for a special occasion there is nothing quite like a hair accessory. I have designed a range of head bands and hair accessories.<br />\nI am also a bit of a<strong> beauty junkie</strong> so I am always on the look out from fabulous cult products for my clients. I was the first to bring Tarte and Eve Lom to Australia.</p>\n<p><strong>Hot Hair Tip for the Season:</strong><br />\nLonger hair is definitely making a comeback. The emphasis is on gloss and shine so hair needs to be really healthy. Hair accessories, particularly decorative clips will be big.<br />\nI am reading predictions of<strong> platinum</strong> making a return but I’m not convinced. It requires a lot of maintenance so I think it might not be as popular as some are saying.<br />\nThis year we will see more interesting highlighting techniques like<strong> ‘strobing’</strong> in which the placement of foils is determined by the individual’s face shape, colour and cut. This technique is generating interest but it’s something colour specialist have been practising for some time.<br />\nAs clients become more educated about colouring options, precise highlighting techniques that are custom made to an individuals face shape, hair cut and colour will become more popular.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/03/26160627/George-G-Red-Flower.jpg"},"categories":[{"slug":"hair","name":"Hair"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"26595bf5-6838-54a9-9c7f-b7e32506f892"}}

